import { pdf } from "@react-pdf/renderer";
import dayjs from "dayjs";
import React, { useContext, useState, useEffect } from "react";
import { CrudContext } from "../../../context/CrudContext";
import { aprobacionPlanilla } from "../../../data/dataTable";
import HojaTareoAsociacion from "../../../helpers/HojaTareoAsociacion";
import { notificacion } from "../../../helpers/mensajes";
import Header from "../../header/Header";
import TablaAprobacionPlanilla from "../../tabla/TablaAprobacionPlanilla";
import Buscador from "../Buscador";
import ModalPlanillaControl from "../control/ModalPlanillaControl";
import ObservacionAprobacion from "./ObservacionAprobacion";

const AprobacionPlanillaLayout = () => {
  const [planilla, setPlanilla] = useState([]);
  const { getData, updateData, createData } = useContext(CrudContext);
  const [observacion, setObservacion] = useState(false);

  const getPlanilla = async () => {
    // const response = await getData("planilla/aprobacion");
    const response = await getData("aprobacion");

    if (response) {
      setPlanilla(response.data);
    }
  };

  useEffect(() => {
    getPlanilla();
  }, []);

  const updateAprobacion = async (e, data) => {
    const route = "planilla/asistencia";
    let firma;
    let info;
    if (e.target.checked) {
      firma = JSON.parse(localStorage.getItem("firma"));
    }

    info = {
      [e.target.name]: firma || null,
    };
    let aprobacionId = data.id ? data.id : 0;

    const response = await updateData(info, aprobacionId, route);
    if (response) {
      notificacion(response.status, response.msg);
      getPlanilla();
    }
  };

  const handleAsistencias = async (e) => {
    const info = {
      asociacion_id: e.asociacion_id !== null ? e.asociacion_id : null,
      fecha_inicio: e.fecha_inicio,
      fecha_fin: e.fecha_fin,
      dni: e.dni !== null ? e.dni : null,
    };

    const response = await createData(info, "aprobacion/asistencias");

    if (response) {
      const pdfBlob = await pdf(HojaTareoAsociacion(response.data)).toBlob();

      if (pdfBlob) {
        // Crea una URL a partir del blob
        const pdfUrl = URL.createObjectURL(pdfBlob);

        // Crea un enlace para descargar el archivo
        const link = document.createElement("a");
        link.href = pdfUrl;
        link.download = "tareo.pdf";
        document.body.appendChild(link);

        // Simula un click en el enlace para descargar el archivo
        link.click();

        // Elimina el enlace del documento
        document.body.removeChild(link);

        // Libera la URL creada a partir del blob
        URL.revokeObjectURL(pdfUrl);
      }
    }
  };

  const handleObservacion = () => {
    setObservacion(true);
  };

  const columns = aprobacionPlanilla(
    updateAprobacion,
    handleAsistencias,
    handleObservacion
  );
  return (
    <>
      <Header text={"Aprobaciones"} ruta={"/planilla"} />
      <div className="margenes">
        {/* <Buscador /> */}

        <TablaAprobacionPlanilla columns={columns} table={planilla} />
      </div>
      {observacion && (
        <ObservacionAprobacion modal={observacion} setModal={setObservacion} />
      )}
    </>
  );
};

export default AprobacionPlanillaLayout;
