import { Button, Form } from "antd";
import React from "react";
import { useState } from "react";
import { notificacion } from "../../../helpers/mensajes";
import MainModal from "../../modal/MainModal";
import DragAndDrop from "../../personal/DragAndDrop";
import "../style/modalValidarTrabajador.css";
const RegistroHuella = ({ modal, setModal, data, actualizarTabla }) => {
  const [avatar, setAvatar] = useState(null);

  const closeModal = () => {
    setModal(false);
  };

  const handleSubmit = async () => {
    const formData = new FormData();
    avatar?.file && formData.set("image", avatar.file || "");

    const query = await fetch(
      `${process.env.REACT_APP_BASE}/planilla/huella/${data?.aprobacion_id}`,
      {
        method: "PUT",
        body: formData,
      }
    );
    const response = await query.json();
    if (response) {
      notificacion(response.status, response.msg);
      closeModal();
      actualizarTabla()
    }
  };
  return (
    <MainModal
      title={"Validaci√≥n del trabajador"}
      className="modal-validacion"
      open={modal}
      width={400}
      closeModal={closeModal}
    >
      <Form onFinish={handleSubmit} layout="horizontal">
        <Form.Item>
          <div className="drag-container">
            <DragAndDrop
              avatar={avatar}
              setAvatar={setAvatar}
              selected={data}
            />
          </div>
        </Form.Item>
        <div className="button-container">
          <Button htmlType="submit">Guardar</Button>
        </div>
      </Form>
    </MainModal>
  );
};

export default RegistroHuella;
