import React, { useContext } from "react";
import { useState } from "react";
import { useEffect } from "react";
import { AiOutlineClose, AiFillEye } from "react-icons/ai";
import { PlanillaContext } from "../../../context/PlanillaContext";
import Tabla from "../../tabla/Tabla";
import Buscador from "../Buscador";
import "../style/modalPlanillaControl.css";
import ModalValidacionPagos from "./ModalValidacionPagos";
import ModalValidacionPagosAsociacion from "./ModalValidacionPagosAsociacion";
import { tablePlanillaControl } from "../../../data/dataTable";
import MainModal from "../../modal/MainModal";
import { CrudContext } from "../../../context/CrudContext";

const ModalPlanillaControl = ({
  selected,
  actualizarTabla,
  planillaControl,
  setPlanillaControl,
}) => {
  const [contrato, setContrato] = useState([]);
  const [dataSelected, setDataSelected] = useState([]);
  const { setPago } = useContext(PlanillaContext);
  const { getData } = useContext(CrudContext);
  const [historial, setHistorial] = useState([]);
  const [modalValidacion, setModalValidacion] = useState(false);
  const [modalValidacionAsociacion, setModalValidacionAsociacion] =
    useState(false);

  const closeModal = () => {
    setPlanillaControl(false);
  };

  const getHistorial = async () => {
    if (selected?.dni) {
      const route = `planilla/tareo/${selected.dni}`;
      const response = await getData(route);
      setHistorial(response.data);
    } else {
      const route = `planilla/tareo/asociacion/${selected.asociacion_id}`;
      const response = await getData(route);
      setHistorial(response.data);
    }
  };

  useEffect(() => {
    getHistorial();
  }, [selected]);

  useEffect(() => {
    setContrato([selected]);
  }, [selected]);

  const handleValidacion = (e) => {
    if (selected.codigo === "") {
      setModalValidacionAsociacion(true);
      setDataSelected(e);
    } else {
      setModalValidacion(true);
      setDataSelected(e);
    }
  };

  const handlePagos = (e) => {
    setPago(true);
    setDataSelected(e);
  };

  const columns = tablePlanillaControl(handleValidacion, handlePagos);

  return (
    <MainModal
      className={"modal-planilla"}
      title={"Control de planilla"}
      open={planillaControl}
      width={900}
      closeModal={closeModal}
    >
      <Tabla columns={columns} table={historial} />

      {modalValidacion && (
        <ModalValidacionPagos
          data={dataSelected}
          modal={modalValidacion}
          setModal={setModalValidacion}
          actualizarTabla={getHistorial}
        />
      )}
      {modalValidacionAsociacion && (
        <ModalValidacionPagosAsociacion
          data={dataSelected}
          modal={modalValidacionAsociacion}
          setModal={setModalValidacionAsociacion}
          actualizarTabla={getHistorial}
        />
      )}
    </MainModal>
  );
};

export default ModalPlanillaControl;
